import React, { useEffect } from "react";
import { socket } from "./SockectConnect";
import { useSelector, useDispatch } from "react-redux";
import { FetchStaffs } from "../Network/CredApi";
import { FetchModes } from "../Network/ModeApi";
import { FetchExpenseCats } from "../Network/ExpenseCatApi";
import { FetchExpenses } from "../Network/ExpenseApi";
import { FetchBills } from "../Network/BillApi";
import { FetchBuildings } from "../Network/BuildingApi";
import { FetchHouses } from "../Network/HouseApi";
import { FetchInvoices } from "../Network/InvoiceApi";
import { FetchOwners } from "../Network/OwnerApi";
import { FetchPayments } from "../Network/PaymentApi";
import { FetchSubs } from "../Network/SubApi";
import { FetchNotifications } from "../Network/NotificationApi";
import { FetchMpesaPayments } from "../Network/MpesaApi";
import { FetchMessages } from "../Network/MessageApi";

const SocketMessages = () => {
	//redux dispatch
	const dispatch = useDispatch();

	const User = useSelector((state) => state.auth.user);
	const ActiveCred = useSelector((state) => state.cred.activeCred);
	const online = useSelector((state) => state.online.online);

	useEffect(() => {
		socket.on("message", async (message) => {
			if (message.messageType === "notification") {
				if ("serviceWorker" in navigator && "PushManager" in window) {
					navigator.serviceWorker.ready
						.then((registration) => {
							registration.showNotification(message.dataValues.title, {
								body: message.dataValues.note,
								icon: "",
							});
						})
						.catch((err) => console.log(err));
				}
				FetchNotifications(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "staff") {
				FetchStaffs(
					ActiveCred.instLinker,
					User.token,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "mode") {
				FetchModes(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}

			if (message.messageType === "expenseCat") {
				FetchExpenseCats(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "expense") {
				FetchExpenses(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "bill") {
				FetchBills(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "building") {
				FetchBuildings(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "house") {
				FetchHouses(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "invoice") {
				FetchInvoices(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "owner") {
				FetchOwners(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "payment") {
				FetchPayments(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "online-payment") {
				FetchMpesaPayments(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "tenant") {
				FetchStaffs(
					ActiveCred.instLinker,
					User.token,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "sub") {
				FetchSubs(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
			if (message.messageType === "sms") {
				FetchMessages(
					User.token,
					ActiveCred.instLinker,
					dispatch,
					online,
					"socket",
					message.dataValues
				);
			}
		});
	}, []);

	return <></>;
};

export default SocketMessages;
